import Vuex from 'vuex'
import Vue from 'vue';

import websocketPlugin from './plugins/web-socket'

Vue.use(Vuex);

export default new Vuex.Store({
  strict: true,

  state: {
    Cmd: {
      CMD_SET_CREDENTIAL : "02"
    },

    websocketIsConnected: false,
    websocket: null,

    // IP Information
    ipInfo: {
      IsDhcp: true,
      Address: "192.168.77.77",
      Subnet: "255.255.255.0",
      Gateway: "192.168.77.1",
      MAC: "48:65:EE:48:01:02"
    },

    // Unit Options
    unitOptions: {
      Tx: {
        StepAtten: {
          Min: 0.0,
          Max: 31.75,
          Rez: 0.25
        },

        LoFreq: {
          PresentEnabled: true,
          RangeEnabled: false,
          Presets: [28.05, 29.00]
        },

        Alc: {
          Enabled: false,
          Active: false,  
        }
      }
    },

    // Unit Status
    unitStatus: {
      TxStepAtten: 2.75,
      TxLoFreq: 28.05,
      TxPower: 31.5,
      TxRefLocked: true,
      SwTxEnabled: true,

      RxStepAtten: 0.00,
      RxLoFreq: 0.0,
      RxPower: 0,
      RxRefLocked: true,
      SwRxEnabled: false,

      FanSpeed: 2130,
      Temperature: 53.2,

      TempCompEnabled: true,

      // Faults: ['Master', 'Drain Voltage', 'Fan Power']
      Faults: []
    },
    savedUnitStatus: {
      TxStepAtten: 0.00,
      TxLoFreq: 28.05,
      TxPower: 31.5,
      TxRefLocked: true,
      SwTxEnabled: true,

      RxStepAtten: 0.00,
      RxLoFreq: 0.0,
      RxPower: 0,
      RxRefLocked: true,
      SwRxEnabled: false,

      FanSpeed: 2130,
      Temperature: 53.2,

      TempCompEnabled: true,
      Faults: []
    },

    // Unit Information
    unitInfo: {
      Model: "KAA-100B-A1-1130110",
      Serial: "SN2310",
      Firmware: "20210102BD04",
      Bootloader: "0.2.2"
    },

    // NTP Options
    clockNtp: {

      PrimaryAddr: "192.168.10.100",
      SecondaryAddr: "192.168.10.100",
      
      ManualTimeEnabled: true,
      ManualDateTime: 1634781386000,

      UtcOffsetMinute: 90,

      UpdateDaily: false,
      IsSynced: false
    },

    // Serial Interface - RS485, Baudrate
    serialInterface: {
      IsRs485: true,
      SelectedBaudrate: 115200,
      BaudrateOptions: [115200, 38400, 9600]
    },

    // RF Startup Settings
    rfStartupSettings: {
      BootupRf: false,
      RfOffset: 3.7,
      PowerConversion: 1,
    },

    alias: "Mission BUC"

  },

  getters: {
    getUnitInfo: function(state) {
      return state.unitInfo;
    },

    getTxOption: function(state) {
      return state.unitOptions.txOption;
    },

    getUnitStatus: function(state) {
      return state.unitStatus;
    },

    getIpInfo: function(state) {
      return state.ipInfo;
    },

    getSerialOptions: function(state) {
      var options = JSON.parse(JSON.stringify(state.serialInterface));
      return options;
    },

    getRfSettings: function(state) {
      return state.rfStartupSettings;
    },
    getRfOffset: function(state) {
      return state.RfOffset;
    },

    getClockNtp: function(state) {
      return state.clockNtp;
    },

    getUnitAlias: function(state) {
      return state.alias;
    }
  },

  mutations: {

    SET_WEBSOCKET(state, newWebsocket) {
      state.websocket = newWebsocket;
    },
    SET_WEBSOCKET_CONNECTED(state, connected) {
      state.websocketIsConnected = connected;
    },
    
    SET_IP_INFO(state, newIpInfo) {
      state.ipInfo = newIpInfo;
    },

    SET_STEP_ATTENUATOR(state, newAtten) {
      state.unitStatus.TxStepAtten = newAtten;
    },

    SET_SW_TX_ENABLE(state, isEnabled) {
      state.unitStatus.SwTxEnabled = isEnabled;
    },

    SET_SERIAL_INTERFACE_OPTIONS(state, options) {
      state.serialInterface.SelectedBaudrate = options.SelectedBaudrate;
      state.serialInterface.IsRs485 = options.IsRs485;
    },

    SET_RF_OFFSET_SETTINGS(state, rfOffset) {
      state.rfStartupSettings.RfOffset = rfOffset;
    },
    SET_BOOTUP_RF_SETTINGS(state, onRf) {
      state.rfStartupSettings.BootupRf = onRf;
    },
    SET_RF_POWER_CONVERSION(state, conversion) {
      state.rfStartupSettings.PowerConversion = conversion;
    },

    // Test Faults
    SET_TEST_FAULT(state) {
      state.savedUnitStatus.Faults = state.unitStatus.Faults;
      state.savedUnitStatus.Temperature = state.unitStatus.Temperature;

      state.unitStatus.Faults = ["Master", "OverTemp"];
      state.unitStatus.Temperature = 110.4;
    },
    RESET_TEST_FAULT(state) {
      state.unitStatus.Faults = state.savedUnitStatus.Faults;
      state.unitStatus.Temperature = state.savedUnitStatus.Temperature;
    },

    SET_UNIT_ALIAS(state, name) {
      state.alias = name;
    },

    SET_CREDENTIAL(state, credential) {
      var msg = {
        cmd: state.Cmd.CMD_SET_CREDENTIAL,
        cmdData: credential
      };
      state.websocket.send(JSON.stringify(msg));
    }
  },

  actions: {
    PerformSetIpInfo({commit}, newIpInfo) {
      commit("SET_IP_INFO", newIpInfo);
    },

    PerformSetStepAtten({commit}, newStepAtten) {
      commit("SET_STEP_ATTENUATOR", newStepAtten);
    },

    PerformSetSwTxEnabled({commit}, isEnabled) {
      commit("SET_SW_TX_ENABLE", isEnabled);
    },

    PerformSetSerialInterface({commit}, serialOptions) {
      commit("SET_SERIAL_INTERFACE_OPTIONS", serialOptions);
    },

    PerformSetRfOffsetSettings({commit}, rfOffset) {
      commit("SET_RF_OFFSET_SETTINGS", rfOffset);
    },
    PerformSetBootupRfSettings({commit}, bootup) {
      commit("SET_BOOTUP_RF_SETTINGS", bootup);
    },
    PerformSetRfPowerConversion({commit}, conversion) {
      commit("SET_RF_POWER_CONVERSION", conversion);
    },

    // Test Faults
    PerformTestFaults({commit}) {
      commit("SET_TEST_FAULT");
    },
    PerformResetFaults({commit}) {
      setTimeout(() => {
        commit("RESET_TEST_FAULT");
      }, 5000);
    },

    PerformSetUnitAlias({commit}, newName) {
      commit("SET_UNIT_ALIAS", newName);
    },

    PerformSetCredential({commit}, credential) {
      commit("SET_CREDENTIAL", credential);
    }
  },

  plugins: [websocketPlugin]
})
