export default function websocketPlugin(store) {
  function connect() {
    
    console.log(store);

    // var url = "ws://" + location.hostname + ":28080/websocket";
    var url = "ws://192.168.235.56:28080/websocket";

    var websocket = new WebSocket(url);
    // var timerPromise = null;

    // Received Data from Web Socket
    websocket.onmessage = function(event) {
      console.log('On Received:', event);
      var dataReceived = JSON.parse(event.data);
      console.log('On Received Data:', dataReceived);
      // store.commit('PROCESS_RECEIVED_DATA', dataReceived);
    }

    // Web Socket Is Connected
    websocket.onopen = function(event) {
      console.log(event);
      store.commit('SET_WEBSOCKET_CONNECTED', true);
      // console.log("Successfully Connected");
      // console.log(event);
      
      // timerPromise = setInterval(timerFunction, 500);
    }

    // Web Socket Is Closed
    websocket.onclose = function(event) {
      console.log(event);
      store.commit('SET_WEBSOCKET_CONNECTED', false);
      // console.log("Connected Closed");
      // console.log(event);

      // if (timerPromise != null) {
      //   clearInterval(timerPromise);
      // }

      // // Reconnect in 5 second
      // setTimeout(function() {
      //   connect();
      // }, 5000);
    }

    websocket.onerror = function(event) {
      console.log(event);
      // store.commit('SET_WEBSOCKET_CONNECTED', false);
      // console.log("Connected Errored");
      // console.log(event);

      // if (timerPromise != null) {
      //   clearInterval(timerPromise);
      // }
    }

    store.commit("SET_WEBSOCKET", websocket);

    // var timerFunction = function() {
    //   if (store.getters.getUnitInfo.Firmware == null) {
    //     store.commit('GET_UNIT_INFO');
    //   }
    //   else if (store.getters.getIpConfigurations.Address == null) {
    //     store.commit('GET_IP_INFORMATION');
    //   }
    //   else {
    //     store.commit('GET_UNIT_STATUS');
    //   }
    // }
  }

  connect();
}